import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: () => import(/* 首页: "/" */ '@/components/Index.vue')
    },
    {
      path: '/user',
      name: 'User',
      component: () => import(/* 用户管理: "user" */ '@/components/User.vue')
    },
    {
      path: '/log',
      name: 'Log',
      component: () => import(/* 日志: "log" */'@/components/Log.vue')
    },
    {
      path: '/carManagement',
      name: 'CarManagement',
      component: () => import(/* 汽车管理: "carManagement" */'@/components/CarManagement.vue')
    },
    {
      path: '/carInfo',
      name: 'CarInfo',
      component: () => import(/* 汽车信息: "carInfo" */'@/components/CarInfo.vue')
    },
    {
      path: '/newsList',
      name: 'NewsList',
      component: () => import(/* 消息列表: "newsList" */'@/components/NewsList.vue')
    },
    {
      path: '/passAnnal',
      name: 'PassAnnal',
      component: () => import(/* 通行记录: "passAnnal" */'@/components/PassAnnal.vue')
    },
    // {
    //   path: '/passAnnal',
    //   name: 'PassAnnal',
    //   component: () => import(/* 通行记录: "passAnnal" */'@/components/PassAnnal.vue')
    // },
    {
      path: '/inspectionAnnal',
      name: 'InspectionAnnal',
      component: () => import(/* 检查记录: "inspectionAnnal" */'@/components/InspectionAnnal.vue')
    },
    {
      path: '/delCarList',
      name: 'DelCarList',
      component: () => import(/* 已删除车辆列表: "delCarList" */'@/components/DelCarList.vue')
    },
    {
      path: '/application',
      name: 'Application',
      component: () => import(/* 审批申请: "application" */'@/components/Application.vue')
    },
    {
      path: '/applicationList',
      name: 'ApplicationList',
      component: () => import(/* 审批列表: "applicationList" */'@/components/ApplicationList.vue')
    },
    {
      path: '/reviewPrimary',
      name: 'ReviewPrimary',
      component: () => import(/* 初级审核: "reviewPrimary" */'@/components/ReviewPrimary.vue')
    },
    {
      path: '/reviewLevelOne',
      name: 'ReviewLevelOne',
      component: () => import(/* 一级审核: "reviewPrimary" */'@/components/ReviewLevelOne.vue')
    },
    {
      path: '/reviewLevelTow',
      name: 'ReviewLevelTow',
      component: () => import(/* 二级审核: "reviewLevelTow" */'@/components/ReviewLevelTow.vue')
    },
    {
      path: '/reviewDetails',
      name: 'ReviewDetails',
      component: () => import(/* 审核页面: "reviewDetails" */'@/components/ReviewDetails.vue')
    },
    {
      path: '/batchImportCar',
      name: 'BatchImportCar',
      component: () => import(/* 车辆批量导入: "batchImportCar" */'@/components/BatchImportCar.vue')
    },
    {
      path: '/outOfService',
      name: 'OutOfService',
      component: () => import(/* 车辆停运: "outOfService" */'@/components/OutOfService.vue')
    },
    {
      path: '/deleteCar',
      name: 'DeleteCar',
      component: () => import(/* 车辆删除: "deleteCar" */'@/components/DeleteCar.vue')
    },
    {
      path: '/outOfServiceCarExport',
      name: 'OutOfServiceCarExport',
      component: () => import(/* 停运车辆查询导出: "outOfServiceCarExport" */'@/components/OutOfServiceCarExport.vue')
    },
    {
      path: '/inspectionProportion_1',
      name: 'InspectionProportion_1',
      component: () => import(/* 检查比例修改: "inspectionProportion_1" */'@/components/InspectionProportion_1.vue')
    },
    {
      path: '/inspectionProportion_2',
      name: 'InspectionProportion_2',
      component: () => import(/* 检查比例修改: "inspectionProportion_2" */'@/components/InspectionProportion_2.vue')
    },
    {
      path: '/trafficStatistics',
      name: 'TrafficStatistics',
      component: () => import(/* 通行统计: "trafficStatistics" */'@/components/TrafficStatistics.vue')
    },
    {
      path: '/entrainmentRecording',
      name: 'EntrainmentRecording',
      component: () => import(/* 夹带记录: "entrainmentRecording" */'@/components/EntrainmentRecording.vue')
    },
    {
      path: '/enterpriseList',
      name: 'EnterpriseList',
      component: () => import(/* 企业审批权备案查询与处理: "enterpriseList" */'@/components/EnterpriseList.vue')
    },
    {
      path: '/enterpriseAuthorization',
      name: 'EnterpriseAuthorization',
      component: () => import(/* 企业审批权申请: "enterpriseAuthorization" */'@/components/EnterpriseAuthorization.vue')
    },
    {
      path: '/approvalProgress',
      name: 'ApprovalProgress',
      component: () => import(/* 审批进度查询: "approvalProgress" */'@/components/ApprovalProgress.vue')
    },
    {
      path: '/violationList',
      name: 'ViolationList',
      component: () => import(/* 违规上报查询及处理: "violationList" */'@/components/ViolationList.vue')
    },
    {
      path: '/sendMessage',
      name: 'SendMessage',
      component: () => import(/* 公共消息推送: "sendMessage" */'@/components/SendMessage.vue')
    },
  ]
})



